

.header{

  grid-area: header;

}  



.main{

  grid-area: main;
  height: 100%;
} 



.footer{

  grid-area: footer;
}
.navbar{
  grid-area: navbar;
}
.header{
  width: 100%;
  grid-area: header;
  position:absolute;
  display: flex;
  justify-content: flex-end;
 
}
a{
  color: white;
}
.trabaja :hover, .terminos :hover, .ayuda :hover, .defensa :hover{
  color: white;
}
.grid-container {
  display: grid;
  min-height: 100%;


  grid-template-areas: 
  
  "navbar main" 
  
  "footer footer";
  grid-template:
  
  "navbar main" auto
  "footer footer" /
  20% 80%;
  grid-template-rows: auto;
}
.trabaja{
  cursor: pointer;
  grid-area: trabaja;
}

.terminos{
  cursor: pointer;
  grid-area: terminos;
}
.ayuda{
  cursor: pointer;
  grid-area: ayuda;
}
.defensa{
  cursor: pointer;
  grid-area: defensa;
}

.copyright{
  padding-top: 20px;
 
}
.container-footer{
  text-align: center;
}
.footer-grid {
  display: grid;
  min-height: 100%;
  justify-content: center;
  text-align: center;
  
  padding:1%;
  gap:4%;
  
  grid-template-areas: 
  
  "trabaja terminos ayuda defensa" 
  
  ;

  grid-template:
  "trabaja terminos ayuda defensa"  auto
   /
  15% 15% 15% 15%;
 
}




.selector-comidas{
  display: grid;

  grid-template-columns: repeat(auto-fill,minmax(10em,15em));

}

.menutitulo{
  color: white;
  font-size: 40px;
  margin-left: 8%;
  margin-top: 1%;
  margin-bottom: 1%;
  font-family: 'Bebas Neue', cursive;
  letter-spacing: 0.2em;
  
}

summary, li{
 
  cursor: pointer;
  outline:0;


}
li{
  font-family: 'Righteous', cursive;
  
  display: flex;
 
  align-items: center;
  list-style: none;
  margin-bottom: 15px;
  
  font-size: 0.6em;
 
}

span{
  
  font-size: 2em;
  font-family: 'Righteous', cursive;
}





li :hover{
  
  transition: 1s;

  padding-right: 300%;
   
 
  padding-left: 100%;
  
  background-color:  rgb(202, 50, 39);
}
.contenedorMenu{
  display: flex;
}
.menu-HamburgerHouse{
 
 width: 25%;

}

.ubicarSetComidas{
 
  width: 100%;
}

.opcionesdecomidas{
  margin: 0% 5% 5% 5%;
  font-size: 1.5em;
  justify-content: center;

}
.imagenesdelascomidas{
  margin-top: 30px;
  width: 100%;
 
}
.tituloscomidas{
font-size: 1.5em;
margin-top: 20px;
font-family: 'Righteous', cursive;
}
p{
  margin:0;
  font-family: 'Archivo Narrow', sans-serif;
}
.precioscomidas{
font-size: 15px;
}
.botoncompra{
  cursor: pointer;
  border-radius: 10px;
  margin-top: 5px;
  background-color:rgb(255, 153, 0);
  width: 100%;
  height: 40px;
  color:#ad0000;
  font-family: 'Archivo Narrow', sans-serif;
  font-size: 1em;
  font-weight: 600;

}

.botonstore{
  cursor: pointer;

  width: 20em;
}
.botoncontainerapp{
  display: flex;
  margin-top: 40px;
  margin-left: 5%;
  
  
}
.botoncontainerapp img{
  max-width: 20%;
  cursor: pointer;

}
.botoncontainer :hover{
  background-color: #ff1010;
  color: white;
}
.botonformulario{
  cursor: pointer;
  border-radius: 10px;
  margin-top: 50px;
  background: rgb(255,203,42);
  background: linear-gradient(180deg, rgba(255,203,42,1) 0%, rgba(245,158,44,1) 47%, rgba(89,84,79,1) 98%);
  width: 8em;
  height: 70px;
  color:#f7f7f7;
  font-family: 'Archivo Narrow', sans-serif;
  font-size: 2em;
  font-weight: 600;
  margin-left: 5%;

}

.botonformulariocontainer :hover{
  transition: 1s;
  color:rgb(255, 153, 0);
  background: rgb(255,42,42);
  background: linear-gradient(180deg, rgba(255,42,42,1) 0%, rgba(45,42,40,1) 85%);
}
.sumate{
  font-family: 'Righteous', cursive;
  padding-left: 5%;
  color: white;
  font-size: 4em;
  margin: 0;
  padding-top: 40px;
 
 
}
.estiloformulario p{
  padding-left: 5%;
  color: white;
  font-size: 1.5em;
}

.estiloformularioapp p{
  padding-left: 5%;
  color: white;
  font-size: 1.4em;
}

.ventana{
  
  display: grid;
  grid-template-columns: repeat(auto-fill,minmax(15em,20em));
  margin-left: 8%;

  
  

}
.miniventana{
  background-color: rgba(0, 0, 0, 0.521);

  color: white;  
  margin: 10%;
 
  
}


.containerimage :hover{
  transition:0.5s;
  transform: scale(1.50);
  

}
.caracteristicasminiventana{
  margin: 0.5em;

}

figure{
  margin: 0;
}


nav{
  color:white;
}
.redessocialesfooter{
  
  display: grid;
  
  justify-content: center;
  align-items: center;
  
  gap: 20px;
  margin:auto;
  
  height: 90px;
 grid-template-columns: 
  "instagram twitter youtube facebook" 
  
  ;

  grid-template:
  "instagram twitter youtube facebook"  auto
   /
  5% 5% 5% 5%;
}
.instagram,.twitter,.youtube,.facebook{
  margin: auto;
  cursor: pointer;
}
.instagram{
  
  grid-area: instagram;
  
}
.twitter{
  grid-area: twitter;
}
.youtube{
  grid-area: youtube;
}
.facebook{
  grid-area: facebook;
}
hr {border: 0 ; 
  border-top: 4px double rgb(247, 11, 11); 
  width: 95%;
  
}










.homeTitulo{
  color: white;
  font-size: 3.5em;
  letter-spacing: 0.1em;
  padding-left: 10%;
