@media (max-width: 900px) {
  .homeTitulo{
    font-size: 2.5em;
  }
  .botoncontainerapp{
    display: flex;
    margin-top: 20px;
    margin-left: 0%;
    justify-content: center;
    
  }
  fieldset{
    margin-left: 0%;
    margin:auto;
    width: 80%;
  }

  .homeTitulo{

    padding-left: 0;
    margin:auto;
  }
  .Portadacontain{

    margin-left: 0;
    margin: auto;
  }


  .main{
    justify-content: center;
    text-align: center;
  }

  .ventana{
    
    justify-content: center;

  }
  ul{
    margin:0;
    padding: 0;
    
  }
  li :hover{
  
    transition: 1s;
  
    padding-right: 0%;
     
   
    padding-left: 0%;
    
    background-color:  rgb(253, 86, 74);
  }


  .grid-container {

    grid-template:
    
    "navbar"
    "main"
    "footer"
    
  }
  .menu-HamburgerHouse {
    width: 25%;
    margin: auto;
}
  .selector-comidas{
    justify-content: center;
  }
  .ventana{
    
    margin-left: 0;
   
    
    
  }
 .menutitulo{
   margin-left: 0;
 }
 .estiloformulario{
  width: 100%;
  display: grid;
  justify-content: center;
  text-align: center;
 
}
.estiloformularioapp{
  width: 100%;
