
html{

  height: 100%;
}
.grid-body{
  display: grid;
  min-height: 100%;


  grid-template-areas: 
  "root";
  grid-template:
  "root";
  grid-template-rows: auto;
  min-height: 100%;
}
html, body {
  
  margin: 0;
 
 
  

 
  min-width: 100%;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
  
  
}

body{

  background-image: linear-gradient(rgb(0, 0, 0), rgb(70, 69, 69)), url('./assets/img/pexels-samfotograffo-3052362.jpg');

  background-attachment: fixed;
  background-size: cover;
  background-blend-mode: screen;

}

.footer{
  
  width: 100%;
  height: 100%;
  
  background: rgb(218,121,17);

}
